<template>
  <select
    @mousedown.stop
    @wheel.stop
    v-bind="$attrs"
    @change="$emit('update:modelValue', $event.target.value)"
  >
    <slot></slot>
  </select>
</template>

<script>
export default {
  name: 'StopPropagationSelect',
  inheritAttrs: false,
  emits: ['update:modelValue']
};
</script>
