<script setup>
import { Handle, Position } from "@vue-flow/core";
import Multiselect from "vue-multiselect";
import SliderInput from "../SliderInput.vue";
import InputVariables from '../InputVariables.vue';
</script>
<script>
export default {
  name: "MainAINode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
    largeModel: Object,
    plugins: Object,
  },
  components: { Multiselect, SliderInput, InputVariables },
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      largeModelList: this.largeModel,
      pluginsList: this.plugins,
      isFold: true,
      isFold_inputs: true,
      isFold_outputs: true,
      isFold_prompt: true,
      isFold_mainAI: true,
      isFold_plugins: true,
      isFold_plugins2: true,
      isFold_Coefficient: true,
    };
  },
  methods: {
    handleRangeMouseDown() {
      return;
    },
    saveNode() {
      // 使用Object.assign保留原有数据结构
      const updatedData = Object.assign({}, this.data, {
        inputs: this.nodeData.inputs,
        prompt: this.nodeData.prompt,
        label: this.label
      });
      
      this.$emit("saveNode", {
        id: this.id,
        data: updatedData
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
    },
    checkFoldOutputs() {
      this.isFold_outputs = !this.isFold_outputs;
    },
    checkFoldPrompt() {
      this.isFold_prompt = !this.isFold_prompt;
    },
    checkFoldMainAI() {
      this.isFold_mainAI = !this.isFold_mainAI;
    },
    checkFoldPlugins() {
      this.isFold_plugins = !this.isFold_plugins;
    },
    checkFoldPlugins2() {
      this.isFold_plugins2 = !this.isFold_plugins2;
    },
    checkCoefficient() {
      this.isFold_Coefficient = !this.isFold_Coefficient;
    },
    handlePromptInput(event) {
      this.nodeData.prompt = event.target.value;
      this.saveNode();
    },
  },
  watch: {
    'data.sourceNodes': {
      handler(newSourceNodes, oldSourceNodes) {
        // 当sourceNodes发生变化时（比如删除连线）
        if (oldSourceNodes) {
          const removedNodeIds = Object.keys(oldSourceNodes).filter(id => !newSourceNodes || !newSourceNodes[id]);
          if (removedNodeIds.length > 0) {
            // 找出需要删除的变量
            const inputsToRemove = [];
            this.nodeData.inputs.forEach((input, index) => {
              // 检查每个输入变量是否来自被删除的节点
              if (input.sourceId) {
                const [nodeId] = input.sourceId.split('_');
                if (removedNodeIds.includes(nodeId)) {
                  inputsToRemove.push(index);
                }
              }
            });

            // 从后往前删除变量，避免索引变化的问题
            for (let i = inputsToRemove.length - 1; i >= 0; i--) {
              this.nodeData.inputs.splice(inputsToRemove[i], 1);
            }

            // 如果删除后没有输入变量了，添加一个空的
            if (this.nodeData.inputs.length === 0) {
              this.nodeData.inputs.push({
                varname: '',
                type: 'string',
                txt: '',
                sourceId: ''
              });
            }

            // 保存更改
            this.saveNode();
          }
        }
      },
      deep: true
    }
  },
};
</script>
<template>
  <Handle class="Handle" type="target" :position="Position.Left" :style="{}" />
  <Handle class="Handle" type="source" :position="Position.Right" :style="{}" />
  <div class="NodeBox MainAINode iconfont" :class="[isSelected ? 'Selected' : '', '']">
    <div class="Title">
      <span class="Icon fold" :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']" @click="checkFold"></span>
      <span class="Icon ticon icon-MainAI"></span>
      <span>主控AI</span>
      <input class="label" v-model="label" value="{{label}}" placeholder="备注" @blur="saveLabel" />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">主导应用的核心AI，可以调用多个Agent</div>
      <div class="LargeModel Content">
        <div class="Title">
          <span class="Icon fold" :class="[isFold_mainAI ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldMainAI"></span>
          <span>模型</span>
        </div>
        <div v-if="isFold_mainAI">
          <select v-model="nodeData.model.LargeModelID">
            <option v-for="(item, i) in largeModelList" :key="i" :value="item.LargeModelID">
              {{ item.ModelOrganizationName }}({{ item.Name }})
            </option>
          </select>
        </div>
      </div>

      <div class="Inputs Content">
        <div class="Title">
          <span class="Icon fold" :class="[isFold_plugins ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPlugins"></span>
          <span>提示词函数(插件)</span>
        </div>
        <div v-if="isFold_plugins">
          <multiselect v-model="nodeData.SemanticFunction" placeholder="请选择函数" :multiple="true" :close-on-select="false"
            :clear-on-select="false" :preserve-search="true" label="Name" track-by="Name" taggable="true"
            :options="pluginsList.filter((value) => value.PluginsType == 1)"></multiselect>
        </div>
      </div>
      <div class="Plugins Content">
        <div class="Title">
          <span class="Icon fold" :class="[isFold_plugins2 ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPlugins2"></span>
          <span>原生函数(插件)</span>
        </div>
        <div v-if="isFold_plugins2">
          <multiselect v-model="nodeData.NativeFunction" placeholder="请选择函数" :multiple="true" :close-on-select="false"
            :clear-on-select="false" :preserve-search="true" label="Name" track-by="Name" taggable="true"
            :options="pluginsList.filter((value) => value.PluginsType == 2)"></multiselect>
        </div>
      </div>


      <InputVariables 
      v-model="nodeData.inputs" 
      :textarea="$refs.promptTextarea"
      :sourceNodes="data.sourceNodes" 
      @save="saveNode" />

      <div class="Prompt Content">
        <div class="Title">
          <span class="Icon fold" :class="[isFold_prompt ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPrompt"></span>
          <span>提示词（将输入变量与提示词整理，并让AI重新生成返回结果）</span>
        </div>
        <div v-if="isFold_prompt">
          <textarea 
          v-model="nodeData.prompt" 
          ref="promptTextarea"
          @input="handlePromptInput"
          @mousedown.stop="handleRangeMouseDown" @wheel.stop="handleRangeMouseDown"
            :placeholder="[
              '需要将变量拼接在待返回的字符串中：' +
              nodeData.inputs
                .filter(item => item.sourceId && item.varname)
                .map((item) => '{{' + item.sourceId + '}}')
                .join(','),
            ]"></textarea>
        </div>
      </div>
      <div class="Coefficient Content">
        <div class="Title">
          <span class="Icon fold" :class="[isFold_Coefficient ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkCoefficient"></span>
          <span>系数设置</span>
        </div>
        <div v-if="isFold_Coefficient">
          <el-form label-width="auto" style="max-width: 600px" status-icon>
            <el-form-item label="随机性(值越大越有创造力)">
              <SliderInput v-model="nodeData.temperature" min="0" max="100" />
            </el-form-item>
            <el-form-item label="多样性(值越大越有多样性)">
              <SliderInput v-model="nodeData.topp" min="0" max="100" />
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="Outputs Content">
        <div class="Title">
          <span class="Icon fold" :class="[isFold_outputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldOutputs"></span>
          <span>输出</span>
        </div>
        <div v-if="isFold_outputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.output" :key="i">
                <td><input v-stop-propagation v-model="item.varname" /></td>
                <td><input v-stop-propagation v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>