export default {
  mounted(el) {
    const handleEvent = (e) => {
      e.stopPropagation();
    };

    // 找到目标元素（可能是el本身或者其子元素）
    const targetEl = el.tagName.toLowerCase() === 'select' || el.tagName.toLowerCase() === 'input' 
      ? el 
      : el.querySelector('select, input');

    if (targetEl) {
      // 添加事件监听
      targetEl.addEventListener('mousedown', handleEvent);
      targetEl.addEventListener('wheel', handleEvent);
      targetEl.addEventListener('keydown', handleEvent);
      targetEl.addEventListener('keyup', handleEvent);
      targetEl.addEventListener('input', handleEvent);

      // 保存事件处理函数和目标元素以便在卸载时移除
      el._stopPropagation = {
        handler: handleEvent,
        target: targetEl
      };
    }
  },
  unmounted(el) {
    // 清理事件监听
    if (el._stopPropagation) {
      const { handler, target } = el._stopPropagation;
      target.removeEventListener('mousedown', handler);
      target.removeEventListener('wheel', handler);
      target.removeEventListener('keydown', handler);
      target.removeEventListener('keyup', handler);
      target.removeEventListener('input', handler);
      delete el._stopPropagation;
    }
  }
};
