<!-- 输入变量选择组件 -->
<template>
  <div class="Inputs Content">
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="toggleFold"
      ></span>
      <span>输入变量</span>
    </div>
    <div v-if="isFold">
      <table>
        <thead>
          <th>变量名</th>
          <th>类型</th>
          <th>操作</th>
        </thead>
        <tbody>
          <tr v-for="(item, i) in modelValue" :key="i">
            <td>
              <select 
                v-stop-propagation
                v-model="item.sourceId" 
                @change="onVarnameSelect(item, $event.target.value)"
                class="VarnameSelect"
              >
                <option value="" disabled>请选择变量名</option>
                <optgroup 
                  v-for="(group, nodeId) in groupVarnamesBySource(i)" 
                  :key="nodeId"
                  :label="group.label || nodeId"
                >
                  <option 
                    v-for="varname in group.variables" 
                    :key="varname.sourceId" 
                    :value="varname.sourceId"
                  >
                    {{ varname.displayText }}
                  </option>
                </optgroup>
              </select>
            </td>
            <td>
              <input v-stop-propagation type="text" v-model="item.type" readonly />
            </td>
            <td>
              <span class="DeleteBtn" @click="removeInput(i)">×</span>
              <span class="InsertBtn" v-if="textarea && item.sourceId" @click="insertToPrompt(item)">→</span>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="AddBtn" @click="addInput">+</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'InputVariables',
  props: {
    modelValue: {
      type: Array,
      default: () => []
    },
    sourceNodes: {
      type: Object,
      default: () => ({})
    },
    textarea: {
      type: Object,
      default: null
    }
  },
  emits: ['update:modelValue', 'save'],
  data() {
    return {
      isFold: true
    };
  },
  methods: {
    toggleFold() {
      this.isFold = !this.isFold;
    },
    getAvailableVarnames(currentIndex) {
      const availableVarnames = [];
      // 获取已使用的变量ID列表
      const usedSourceIds = this.modelValue
        .map((input, index) => (index !== currentIndex ? input.sourceId : null))
        .filter(Boolean);

      if (this.sourceNodes) {
        // 遍历所有源节点
        Object.entries(this.sourceNodes).forEach(([nodeId, source]) => {
          if (source.output) {
            source.output.forEach(output => {
              // 使用源节点中的id
              const sourceId = output.id;
              // 如果变量ID未被使用
              if (!usedSourceIds.includes(sourceId)) {
                availableVarnames.push({
                  varname: output.varname,
                  displayText:output.displayText,
                  nodeId: nodeId,
                  label: source.label,
                  sourceId: sourceId // 使用源节点中的id
                });
              }
            });
          }
        });
      }

      // 按节点标签和变量名排序
      return availableVarnames.sort((a, b) => {
        const labelCompare = (a.label || a.nodeId).localeCompare(b.label || b.nodeId);
        if (labelCompare !== 0) return labelCompare;
        return a.varname.localeCompare(b.varname);
      });
    },
    groupVarnamesBySource(currentIndex) {
      const varnames = this.getAvailableVarnames(currentIndex);
      const groups = {};
      
      varnames.forEach(varname => {
        const key = varname.nodeId;
        if (!groups[key]) {
          groups[key] = {
            label: varname.label,
            variables: []
          };
        }
        groups[key].variables.push({
          ...varname,
          displayText: varname.displayText || `${varname.varname} (${varname.label})`,
          value: varname.sourceId
        });
      });
      
      return groups;
    },
    onVarnameSelect(item, selectedSourceId) {
      
      // 从所有可用变量中找到选中的变量
      const allVarnames = this.getAvailableVarnames(
        this.modelValue.findIndex(input => input === item)
      );
      const selectedVar = allVarnames.find(v => v.sourceId === selectedSourceId);
      
      if (selectedVar) {
        item.varname = selectedVar.varname;
        item.sourceId = selectedVar.sourceId;
        item.type = 'string';  // 默认为字符串类型
        item.txt = '';
        this.$emit('save');
      }
      
    },
    addInput() {
      const newInputs = [...this.modelValue, {
        varname: '',
        type: 'string',
        txt: '',
        sourceId: ''
      }];
      this.$emit('update:modelValue', newInputs);
      this.$emit('save');
    },
    removeInput(index) {
      const newInputs = [...this.modelValue];
      newInputs.splice(index, 1);
      
      // 如果删除后没有输入变量了，添加一个空的
      if (newInputs.length === 0) {
        newInputs.push({
          varname: '',
          type: 'string',
          txt: '',
          sourceId: ''
        });
      }
      
      this.$emit('update:modelValue', newInputs);
      this.$emit('save');
    },
    insertToPrompt(item) {
      if (!item.sourceId || !this.textarea) return;
      
      const text = `{{${item.sourceId}}}`;
      const textarea = this.textarea;
      const start = textarea.selectionStart;
      const end = textarea.selectionEnd;
      const value = textarea.value || '';
      
      // 计算新值
      let newValue;
      if (typeof start === 'number' && typeof end === 'number') {
        // 在光标位置插入
        newValue = value.substring(0, start) + text + value.substring(end);
      } else {
        // 追加到末尾
        newValue = value + text;
      }

      // 更新值并触发input事件
      textarea.value = newValue;
      textarea.dispatchEvent(new Event('input', { bubbles: true }));
      
      // 更新光标位置并聚焦
      textarea.focus();
      if (typeof start === 'number') {
        const newPosition = start + text.length;
        textarea.setSelectionRange(newPosition, newPosition);
      }
    },
  }
};
</script>
