<script setup>
import { Handle, Position } from "@vue-flow/core";
import InputVariables from '../InputVariables.vue';
</script>
<script>
export default {
  name: "AgentNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
    agents: Object,
  },
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      agentList: this.agents,
      isFold: true,
      isFold_inputs: true,
      isFold_outputs: true,
      isFold_Agent: true,
      isFold_prompt: true,
    };
  },
  components: {
    Handle,
    InputVariables
  },
  methods: {
    handleRangeMouseDown() {
      return;
    },
    saveNode() {
      // 使用Object.assign保留原有数据结构
      const updatedData = Object.assign({}, this.data, {
        inputs: this.nodeData.inputs,
        prompt: this.nodeData.prompt,
        label: this.label
      });
      
      this.$emit("saveNode", {
        id: this.id,
        data: updatedData
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
    },
    checkFoldOutputs() {
      this.isFold_outputs = !this.isFold_outputs;
    },
    checkFoldAgent() {
      this.isFold_Agent = !this.isFold_Agent;
    },
    checkFoldPrompt() {
      this.isFold_prompt = !this.isFold_prompt;
    },
    handlePromptInput(event) {
      this.nodeData.prompt = event.target.value;
      this.saveNode();
    },
  },
  watch: {
    'data.sourceNodes': {
      handler(newSourceNodes, oldSourceNodes) {
        // 当sourceNodes发生变化时（比如删除连线）
        if (oldSourceNodes) {
          const removedNodeIds = Object.keys(oldSourceNodes).filter(id => !newSourceNodes || !newSourceNodes[id]);
          if (removedNodeIds.length > 0) {
            // 找出需要删除的变量
            const inputsToRemove = [];
            this.nodeData.inputs.forEach((input, index) => {
              // 检查每个输入变量是否来自被删除的节点
              if (input.sourceId) {
                const [nodeId] = input.sourceId.split('_');
                if (removedNodeIds.includes(nodeId)) {
                  inputsToRemove.push(index);
                }
              }
            });

            // 从后往前删除变量，避免索引变化的问题
            for (let i = inputsToRemove.length - 1; i >= 0; i--) {
              this.nodeData.inputs.splice(inputsToRemove[i], 1);
            }

            // 如果删除后没有输入变量了，添加一个空的
            if (this.nodeData.inputs.length === 0) {
              this.nodeData.inputs.push({
                varname: '',
                type: 'string',
                txt: '',
                sourceId: ''
              });
            }

            // 保存更改
            this.saveNode();
          }
        }
      },
      deep: true
    }
  },
};
</script>
<template>
  <Handle class="Handle" type="target" :position="Position.Left" :style="{}" />
  <Handle class="Handle" type="source" :position="Position.Right" :style="{}" />
  <div class="NodeBox Agent iconfont" :class="[isSelected ? 'Selected' : '', '']">
    <div class="Title">
      <span class="Icon fold" :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']" @click="checkFold"></span>
      <span class="Icon ticon icon-Agent"></span>
      <span>Agent</span>
      <input class="label" v-model="label" value="{{label}}" placeholder="备注" @blur="saveLabel" />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">调用Agent可以实现更多复杂组合</div>
      <div class="Agent Content">
        <div class="Title">
          <span class="Icon fold" :class="[isFold_Agent ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldAgent"></span>
          <span>Agent</span>
        </div>
        <div v-if="isFold_Agent">
          <select v-model="nodeData.agent.AgentID">
            <option v-for="(item, i) in agentList" :key="i" :value="item.AgentID">
              {{ item.Name }}
            </option>
          </select>
        </div>
      </div>


      <InputVariables 
      v-model="nodeData.inputs" 
      :textarea="$refs.promptTextarea"
      :sourceNodes="data.sourceNodes" 
      @save="saveNode" />

      <div class="Prompt Content">
        <div class="Title">
          <span class="Icon fold" :class="[isFold_prompt ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPrompt"></span>
          <span>Agent说明(让主控AI更好的理解Agent的作用，并合理安排触发任务)</span>
        </div>
        <div v-if="isFold_prompt">
          <textarea 
          v-model="nodeData.prompt" 
          ref="promptTextarea"
          @input="handlePromptInput"
          @mousedown.stop="handleRangeMouseDown" @wheel.stop="handleRangeMouseDown"
            :placeholder="[
              '需要将变量拼接在待返回的字符串中：' +
              nodeData.inputs
                .filter(item => item.sourceId && item.varname)
                .map((item) => '{{' + item.sourceId + '}}')
                .join(','),
            ]"></textarea>
        </div>
      </div>
      <div class="Outputs Content">
        <div class="Title">
          <span class="Icon fold" :class="[isFold_outputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldOutputs"></span>
          <span>输出</span>
        </div>
        <div v-if="isFold_outputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>类型</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.output" :key="i">
                <td><input v-stop-propagation v-model="item.varname" readonly /></td>
                <td><input v-stop-propagation v-model="item.type" readonly /></td>
                <td><input v-stop-propagation v-model="item.txt" @change="saveNode" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>