import { createApp } from 'vue'
import App from './App.vue'
import '@/common/style.scss'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import Notifications from '@kyvg/vue3-notification'
import stopPropagation from './directives/stopPropagation';
import StopPropagationSelect from './components/common/StopPropagationSelect.vue';

const workflowParams = window.chatParams || {};

const app = createApp(App, {
    initParams: workflowParams
});
app.use(ElementPlus)
app.use(Notifications);
// 注册全局指令
app.directive('stop-propagation', stopPropagation);
// 注册全局组件
app.component('StopPropagationSelect', StopPropagationSelect);
app.mount('#app');