<script setup>
import { Handle, Position } from "@vue-flow/core";
import InputVariables from '../InputVariables.vue';
</script>
<script>
export default {
  name: "AgentEndNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
  },
  components: {
    InputVariables
  },
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      isFold: true,
      isFold_inputs: true,
      isFold_prompt: true,
    };
  },
  methods: {
    handleRangeMouseDown() {
      return;
    },
    saveNode() {
      // 使用Object.assign保留原有数据结构
      const updatedData = Object.assign({}, this.data, {
        inputs: this.nodeData.inputs,
        prompt: this.nodeData.prompt,
        label: this.label
      });

      this.$emit("saveNode", {
        id: this.id,
        data: updatedData
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
    },
    checkFoldPrompt() {
      this.isFold_prompt = !this.isFold_prompt;
    },
    handlePromptInput(event) {
      this.nodeData.prompt = event.target.value;
      this.saveNode();
    },
  },
  watch: {
    'data.sourceNodes': {
      handler(newSourceNodes, oldSourceNodes) {
        // 当sourceNodes发生变化时（比如删除连线）
        if (oldSourceNodes) {
          const removedNodeIds = Object.keys(oldSourceNodes).filter(id => !newSourceNodes || !newSourceNodes[id]);
          if (removedNodeIds.length > 0) {
            // 找出需要删除的变量
            const inputsToRemove = [];
            this.nodeData.inputs.forEach((input, index) => {
              // 检查每个输入变量是否来自被删除的节点
              for (const nodeId of removedNodeIds) {
                const oldSourceNode = oldSourceNodes[nodeId];
                if (oldSourceNode.output && oldSourceNode.output.some(out => out.varname === input.varname)) {
                  inputsToRemove.push(index);
                  break;
                }
              }
            });

            // 从后往前删除变量，避免索引变化的问题
            for (let i = inputsToRemove.length - 1; i >= 0; i--) {
              this.nodeData.inputs.splice(inputsToRemove[i], 1);
            }

            // 如果删除后没有输入变量了，添加一个空的
            if (this.nodeData.inputs.length === 0) {
              this.nodeData.inputs.push({
                varname: '',
                type: 'string',
                txt: ''
              });
            }

            // 保存更改
            this.saveNode();
          }
        }
      },
      deep: true
    }
  },
};
</script>
<template>
  <Handle class="Handle" type="target" :position="Position.Left" :style="{}" />
  <div class="NodeBox EndNode iconfont" :class="[isSelected ? 'Selected' : '', '']">
    <div class="Title">
      <span class="Icon fold" :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']" @click="checkFold"></span>
      <span class="Icon ticon icon-end"></span>
      <span>结束</span>
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">工作流的结束节点，用于返回工作流运行后的结果信息</div>
      <InputVariables v-model="nodeData.inputs" :sourceNodes="data.sourceNodes" @save="saveNode"
        :textarea="$refs.promptTextarea" />

      <div class="Prompt Content">
        <div class="Title">
          <span class="Icon fold" :class="[isFold_prompt ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPrompt"></span>
          <span>组织返回结果</span>
        </div>
        <div v-if="isFold_prompt">
          <textarea ref="promptTextarea" v-model="nodeData.prompt" @input="handlePromptInput" v-stop-propagation
            :placeholder="[
              '需要将变量拼接在待返回的字符串中：' +
              nodeData.inputs
                .filter(item => item.sourceId && item.varname)
                .map((item) => {
                  const [nodeId, varname] = item.sourceId.split('_');
                  return '{{' + item.sourceId + '}}';
                })
                .join(','),
            ]"></textarea>
        </div>
      </div>
    </div>
  </div>
</template>